import { onBeforeUnmount, ref } from "vue";
import { getScrollOffset } from "@/utils/dom.js";
import { debounce } from "@/utils/optimize";
export function useScroll() {
    // 当滚动距离超过一定是navbar变为黑色
    const scrollRef = ref(false);
    const scrollEvent = function () {
        if (getScrollOffset().y > 10) {
            scrollRef.value = true;
        } else {
            scrollRef.value = false;
        }
    }

    document.addEventListener("scroll", debounce(scrollEvent, 27));
    onBeforeUnmount(() => {
        console.log('onBeforeUnmount');
        document.removeEventListener("scroll", this.scrollEvent);
    })
    return {
        scroll: scrollRef
    }
}