import { ref } from "vue";
import { getLightStar } from "../../api/home";

export function useStar() {
    const starListRef = ref([]);

    getLightStar().then(({ code, data }) => {
        if (code === 200) {
            starListRef.value = data;
        }
    })

    return {
        starList: starListRef
    }
}