import { ref, watch } from "vue";
import { getRecentSong } from "../../api/song";

export function useRecentSong() {
    
    const pageRef = ref(1);
    const sizeRef = ref(10);
    const countRef = ref(0);
    const recentSongRef = ref();
    const loadingRef = ref(false);


    const updateRecentSong = function () {
        loadingRef.value = true;
        getRecentSong({
            page: pageRef.value,
            size: sizeRef.value
        }).then(({ code, data }) => {
            if (code === 200) {
                const { count, rows } = data;
                countRef.value = count;
                recentSongRef.value = rows;
            }
            loadingRef.value = false
        })
    }
    updateRecentSong();

    const handleChangePage = function (newPage) {
        pageRef.value = newPage;
    }

    watch(() => pageRef.value, () => {
        updateRecentSong();
    })

    return {
        handleChangePage,
        recentSong: recentSongRef,
        count: countRef,
        page: pageRef,
        size: sizeRef,
        pageLoading : loadingRef
    }



}