import { ref } from "vue";
import { getTopSong } from "../../api/home";
export  function useTopSong () {
    const topSongListRef = ref([])
    getTopSong().then(({code,data})=>{
        if(code === 200) {
            topSongListRef.value = data;
            console.log(topSongListRef.value);
        }
    });
    return {
        topSongList : topSongListRef,

    }
    
}