import { ref } from "vue";
import store from '../../../store';
import { toLink } from "../../../utils/link";

export function useLoginStatus(isLogin) {
    // 管理用户的登录状态
    const optionRef = ref([]);
    const changeStatus = function (isLogin) {
        if (isLogin) {
            optionRef.value = [{
                label: '个人中心',
                key: "personal",
            }, {
                label: '退出登录',
                key: "exit"
            }]
        } else {
            optionRef.value = [];
        }
    }
    const handleSelect = function (key) {
        if (key === 'personal') {
            toLink("/own"); // 打开个人空间
        } else if (key === 'exit') {
            store.dispatch("User/logout"); // 退出登录
        }
    }
    changeStatus(isLogin);

    return {
        options: optionRef,
        changeStatus,
        handleSelect,
        handleToLogin () {
            console.log("a");
            toLink("/login");
        }
    }
}