import { ref } from "vue";
import { getHotStar } from "../../api/home";
export  function useHotStar () {
    const hotStarRef = ref()
    getHotStar().then(({code,data})=>{
        if(code === 200) {
            hotStarRef.value = data;
        }
    });
    return {
        hotStar : hotStarRef,

    }
    
}