<template>
  <n-dropdown trigger="hover" @select="handleSelect" :options="options">
    <span class="login-span text-ellipsis" @click="toLink('/own')" v-if="userId">{{ username }}</span>
    <span @click="handleToLogin"  class="login-span text-ellipsis" v-else>登录</span>
  </n-dropdown>
</template>

<script>
import { useLoginStatus } from "@/composition/Navbar/user";
import { ref } from "vue";
import { mapState } from "vuex";
import {toLink } from "@/utils/link.js";
export default {
  setup() {
    const loginStatusRef = ref(false); // 拿到登录状态
    return {
      ...useLoginStatus(loginStatusRef.value),
      loginStatus: loginStatusRef,
    };
  },
  computed: {
    ...mapState("User", ["userId", "username"]),
  },
  methods : {
    toLink
  }
};
</script>

<style lang="less" >
@import "~@/styles/myStyle.less";
.login-span {
  width: 100%;
  display: inline-block;
  color: hsla(0, 0%, 100%, 0.65);
  cursor: pointer;
}
.n-dropdown-menu .n-dropdown-option .n-dropdown-option-body {
  &:hover {
    color: @blue!important;
    background-color: @lightblue!important;
  }
}

@media screen and (max-width: 700px) { 
  .login-span {
    color : #1a1a1a;
  }
}
</style>